body {

    font-family: Arial, sans-serif;
  
    margin: 0;
  
    padding: 0;
  
    background-color: #f4f4f4;
  
    color: #333;
  
  }
  
  
  
  .container {
  
    max-width: 960px;
  
    margin: 20px auto;
  
    padding: 20px;
  
    background: white;
  
    border-radius: 8px;
  
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  
  }
  
  
  
  h1 {
  
    text-align: center;
  
    margin-bottom: 20px;
  
  }
  
  
  
  .gallery {
  
    display: grid;
  
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  
    gap: 15px;
  
  }
  
  
  
  .gallery img {
  
    width: 100%;
  
    height: auto;
  
    border-radius: 5px;
  
    transition: transform 0.3s;
  
  }
  
  
  
  .gallery img:hover {
  
    transform: scale(1.05);
  
  }
  
  
  
  .load-more {
  
    display: block;
  
    width: 100%;
  
    margin: 20px auto;
  
    padding: 10px;
  
    font-size: 18px;
  
    background-color: #007BFF;
  
    color: white;
  
    border: none;
  
    border-radius: 5px;
  
    cursor: pointer;
  
  }
  
  
  
  .load-more:hover {
  
    background-color: #0056b3;
  
  }